define(function(require, exports, module) {
    'use strict';

    var dom = require('/lib/jsl/dom');
    var Control = require('/lib/jsl/ui/control');
    var i18n = require('../i18n');

    var version = function (opt) {
        this.$super();
        this.model = opt.model;
    };

    version.prototype.render = function () {
        if (this.node.innerHTML) {
            return this;
        }
        this.add(
            dom.div().addClass('panel-version-content').add(this.model.formatMessage(i18n.i18n.version, { version: this.model.version || '' })),
        );
        return this;
    };

    module.exports = Control.extend(version);
});



