define(function(require, exports, module) {
  'use strict';

  var dom = require('/lib/jsl/dom');
  var Panel = require('/lib/jsl/ui/panel');
  var Action = require('/lib/jsl/ui/action');
  var Log = require('/lib/jsl/log');
  
  var office = require('../office');
  var i18n = require('../i18n');

  var logger = Log.createLogger('INVITE');
  
  var store;
  function getStore() {
    if (!store) {
      store = office.getStore();
      if (!store) {
        throw ({ error: { nooffice: true } });
      }
    }
    return store;
  }
  function setStoreItem(key, item) {
    logger.info('Set store item \'' + key + '\': ' + 'length = ' + item.length);
    getStore().set(key, item);
  }

  function InvitedView(opt) {
    this.$super();
    this.delClass('panel').addClass('panel-invited-view');
    this.model = opt.model;

    this.details = [];
  }
 
  InvitedView.prototype.addDetailItem = function () {
    var name, value;
    this.detailsBox.add(
      dom.div({ cssClass: 'panel-info-item' }).add(
        name = dom.div({ cssClass: 'panel-info-name' }),
        value = dom.div({ cssClass: 'panel-info-value' })
      )
    );
    var res = { name: name, value: value };
    this.details.push(res);
    return res;
  };

  InvitedView.prototype.render = function () {
    var chkDelegateSyncAttributes = { '@type': 'checkbox', '@id': 'chkDelegateSync', '^change': this.onDelegateSyncToggle.bind(this) }
    if (localStorage.getItem('delegateSync')) {
      chkDelegateSyncAttributes['@checked'] = localStorage.getItem('delegateSync');
    }  

    if (!this.node.innerHTML) {
      this.add(
        dom.div().addClass('panel-invited-title').add(this.model.joinMode ? i18n.i18n.titleMeetingDetails : i18n.i18n.titleMeetingDetails2),
        this.detailsBox = dom.div().addClass('panel-invited-info').add(
          dom.div().addClass('panel-info-icon').add(
            this.icon = dom.div()
          ),
          this.title = dom.div().addClass('panel-info-title')
        ),
        this.boxParticipantID = dom.div().addClass('action-item-check').add(
          this.chkParticipantID = dom.input({ '@type': 'checkbox', '@id': 'chkParticipantID', '@disabled': 'disabled', '@checked': 'checked' }),
          this.txtParticipantID = dom.label({ '@for': 'chkParticipantID' }).add(i18n.i18n.actionRequireParticipantID)
        ),
        this.boxReserve = dom.div().addClass('action-item-check').add(
          this.chkReserve = dom.input({ '@type': 'checkbox', '@id': 'chkReserve', '^change': this.onReserve.bind(this) }),
          this.txtReserve = dom.label({ '@for': 'chkReserve' }).add(i18n.i18n.actionReserve)
        ),
        this.boxDelegateSync = dom.div().addClass('action-item-check').add(
          this.chkDelegateSync = dom.input(chkDelegateSyncAttributes),
          this.txtDelegateSync = dom.label({ '@for': 'chkDelegateSync' }).add(i18n.i18n.actionToggleDelSync)
        ),
        dom.div().addClass('actions-list').add(
          this.btnStart = new Action().delClass('action').addClass('action-item action-item-join').add(i18n.i18n.actionStart).set({ click: this.onStart.bind(this) }),
          this.btnRemove = new Action().delClass('action').addClass('action-item action-item-accent').add(i18n.i18n.actionRemoveDetails).set({ click: this.onRemove.bind(this) })
        )
      );
    }
    this.btnRemove.toggleClass('hidden', this.model.joinMode);
    this.btnRemove.toggleClass('disabled', !this.model.canRemoveInvite());
    this.btnRemove.node.innerText = i18n.i18n.actionRemoveDetails;
    this.removingDetails = false;
    return this;
  };

  InvitedView.prototype.update = function (source, title, url, isHost, details, invite) {
    this.invite = invite;
    this.boxReserve.toggleClass('hidden', !isHost || !invite.canReserve);
    this.chkReserve.node.checked = invite.isReserved;
    this.boxParticipantID.toggleClass('hidden', !invite.protectMeetingWithParticipantId);
    
    this.boxDelegateSync.toggleClass('hidden', !isHost || !invite.canReserve); // condition if isDelegator

    this.icon.set({cssClass: 'invited-icon-' + source});
    this.title.setText(title || '');
    this.btnStart.setText(isHost ? i18n.i18n.actionStart : i18n.i18n.actionJoin);
    this.btnStart.toggleClass('disabled', !url);
    this.btnRemove.toggleClass('disabled', !this.model.canRemoveInvite());
    this.btnRemove.node.innerText = i18n.i18n.actionRemoveDetails;
    this.removingDetails = false;
  
    this.details.forEach(function (item) {
      item.name.setText('');
      item.value.node.parentElement.style.display = 'none';
      item.value.setText('');
    });
    var self = this;
    Object.getOwnPropertyNames(details).forEach(function (item, index) {
      var controls = self.details[index] || self.addDetailItem();

      controls.name.setText(item);
      var v = details[item] || '';
      controls.value.node.parentElement.style.display = v ? '' : 'none';
      controls.value.setText(v);
    });

    if (this.btnStart.node.href) {
      this.btnStart.node.href = '#';
    }
    delete this.meetingUrl;
    if (!url) {
      return;
    }
    this.btnStart.delClass('disabled');
    if (url.indexOf('http') !== 0) {
      self.btnStart.node.href = url;
      
      // ACMACOS-10782
      if (navigator.vendor.indexOf('Apple') === 0) {
        self.btnStart.node.setAttribute('target', '_blank');
      }
    } else {
      self.meetingUrl = url;
    }
  };

  InvitedView.prototype.onStart = function () {
    if (this.removingDetails) {
      return;
    }
    if (this.meetingUrl) {
	  logger.info('before_openACW8:' + url);
      window.open(this.meetingUrl, '_blank');
      // this.model.startMeeting();
    }
  };

  InvitedView.prototype.onRemove = function () {
    if (this.removingDetails) {
      return;
    }
    this.btnStart.addClass('disabled');
    this.btnRemove.addClass('disabled');
    this.btnRemove.node.innerText = i18n.i18n.actionRemovingDetails;
    this.removingDetails = true;
    this.model.removeInvite();
  };
  
  InvitedView.prototype.onReserve = function () {
    var reserve = this.chkReserve.node.checked;
    var key = 'VR' + this.invite.topicId;
    if (!reserve) {
      localStorage.setItem(key, 'no_reserve');
    } else {
      localStorage.removeItem(key)
    }

    if (this.removingDetails) {
      return;
    }
    this.btnStart.addClass('disabled');
    this.btnRemove.addClass('disabled');
    this.btnRemove.node.innerText = i18n.i18n.actionRemovingDetails;
    this.removingDetails = true;

    this.model.reapplyInvite().then();
  };

  InvitedView.prototype.onDelegateSyncToggle = function () {
    const delSyncToggled = this.chkDelegateSync.node.checked;

    const cancelToggle = () => (this.chkDelegateSync.node.checked = !this.chkDelegateSync.node.checked);
    const confirmToggle = () => {
      if (delSyncToggled) {
        localStorage.setItem('delegateSync', 'checked');
        setStoreItem('delegateSyncEnabled','true');
      } else {
        localStorage.removeItem('delegateSync');
        setStoreItem('delegateSyncEnabled','false');
      };
    };

    this.model.showDelSyncModal(delSyncToggled, cancelToggle, confirmToggle);
  };

  module.exports = Panel.extend(InvitedView);  
});



